*{
  margin: 0;
  padding:0;
}

body {  

  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  display: flex;
  flex-direction: column;
  height:100vh;
}
.App-Header {
  padding-bottom:2px;
  background-color: #303847;
  font-size: calc(9px + 1vmin);
  color: white;
  display: flex;
  flex-direction:row;
  align-items: center;
  justify-content: space-evenly;

}
main {
  overflow:hidden;
  display:flex;
  flex-grow:1;
  }
canvas {
    background-color:red;
}
.btn {
  display: inline-block;
  background: #535B53;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  font-family: inherit;
}
footer {  
  padding-top: 5px;
  padding-bottom:5px;
  background-color: #303847;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
a {
  color: white;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
